import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';



import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import { createPlace } from '../../api/inventory/placeApi';
import { createGiftCardCategory } from '../../api/payment/giftCards';

const Create = (props) => {

    const [submitting, setSubmitting] = React.useState(false)
    const [categoryData, setCategoryData] = React.useState({
        name: ''
    })


    const [errors, setErrors] = React.useState({});


    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }

    const _categoryDataChange = (e) => {
        console.log('chaing', e.target.value);
        setErrors({ ...errors, [e.target.name]: '' })
        setCategoryData({ ...categoryData, [e.target.name]: e.target.value })
    }



    const _submit = () => {

        if (isEmpty(categoryData.name)) {
            setErrors({ ...errors, name: 'Category name cannot be empty!' });
            return;
        }

        console.log('no errros');

        setSubmitting(true)
        //if no errros api call
        const requestJson = {
            "action": "add",
            "name": categoryData.name
        }

        createGiftCardCategory(requestJson)
            .then((res) => {
                console.log(res)
                if (res.json.status) {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'success', title: 'Gift Card category created successfully!', background: '#dffff0' })
                    props.history.push('/gift-card-categories');
                } else {
                    setSubmitting(false);
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: "#ffe5d6" })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }

    return (
        <>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Gift Card Category</h5>
                            <hr />
                            <Row>
                                <Col md={3}>
                                </Col>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Name*</Form.Label>
                                        <Form.Control name="name" value={categoryData.name} onChange={_categoryDataChange} type="text" placeholder="Category Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={3}>
                                </Col>
                            </Row>

                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/gift-card-categories') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </>
    );
}


export default Create;